/* Layout chính */
.story-layout {
    width: 100%;
    overflow-x: hidden;
    font-family: 'Georgia', serif;
}

/* HERO */
.hero-section {
    height: 100vh;
    width: 100%;
    background-image: url("images/616447304_1413854823710273_5607893527885689186_n.jpg");
    background-size: cover;
    background-position: center;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
}

.hero-overlay {
    background: rgba(0, 0, 0, 0.45);
    padding: 40px 60px;
    border-radius: 12px;
    text-align: center;
    color: white;
}

.hero-overlay h1 {
    font-size: 3rem;
    margin-bottom: 10px;
}

/* TEXT SECTION */
.text-section {
    position: relative;
    width: 100%;
    padding: 100px 0;
    line-height: 1.8;
    font-size: 1.1rem;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.text-section h2 {
    color: #ffd54f;
    margin-bottom: 20px;
    font-family: 'Georgia', serif;
    line-height: 1.8;
    font-size: 2rem;
    font-weight: bold;
}

/* Ảnh nền FULL WIDTH */
.text-section::before {
    content: "";
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    background: url("images/img_1.png") center/cover no-repeat;
    z-index: -2;
}

/* Lớp phủ tối để chữ dễ đọc */
.text-section::after {
    content: "";
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.55);
    z-index: -1;
}

/* Container giới hạn text */
.text-section > * {
    position: relative;
    z-index: 1;
    max-width: 900px;
    width: 100%;
    padding: 0 40px;
}

.text-section h2 {
    color: #ffd54f; /* vàng kim */
    margin-bottom: 20px;
    font-family: 'Georgia', serif;
    line-height: 1.8;
    font-size: 16px;
}

.text-section p {
    margin-bottom: 15px;
}

.text-section strong {
    color: #fff59d;
}

/* Wrapper cho tất cả nội dung text */
.text-section {
    flex-direction: column;
}

/* Alternative background cho section 5 */
.text-section.alt-bg::before {
    background: url("images/img_1.png") center/cover no-repeat;
}

/* IMAGE BREAK (kiểu kể chuyện giống báo điện tử) - FULL WIDTH */
.image-break {
    height: 70vh;
    width: 100%;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
}

/* ẢNH RIÊNG */
.img-1 {
    background-image: url("images/ee3d4f8ff111ea55663ad22694f091d4.jpg");
}

.img-2 {
    background-image: url("images/599670673_4378374059149610_1670181223838855279_n.jpg");
}

.img-3 {
    background-image: url("images/616110753_1376573993677044_4990641346874349935_n.jpg");
}

.img-4 {
    background-image: url("images/615903933_1069310418670873_8682039557470043799_n.jpg");
}

.img-5 {
    background-image: url("images/img.png");
}

/* ===== RESPONSIVE ===== */

/* Tablet */
@media (max-width: 1024px) {
    .text-section {
        padding: 80px 0;
        font-size: 1rem;
    }

    .text-section > * {
        max-width: 750px;
        padding: 0 30px;
    }

    .hero-overlay h1 {
        font-size: 2.5rem;
    }

    .hero-overlay {
        padding: 30px 40px;
    }
}

/* Mobile */
@media (max-width: 768px) {
    .hero-section {
        min-height: 100vh;
        height: auto;
        padding: 80px 20px; /* Adjusted padding */
        text-align: center;
    }

    .hero-overlay {
        padding: 20px 25px; /* Reduced padding */
    }

    .hero-overlay h1 {
        font-size: 2rem; /* Balanced font size */
    }

    .text-section {
        padding: 50px 0; /* Adjusted spacing */
        font-size: 1rem;
    }

    .text-section > * {
        max-width: 90%; /* Slightly reduced width */
        padding: 0 15px; /* Adjusted padding */
    }

    .text-section h2 {
        font-size: 1.4rem; /* Adjusted heading size */
    }

    .image-break {
        height: 45vh; /* Adjusted height */
        background-attachment: scroll;
    }
}

/* Mobile nhỏ */
@media (max-width: 480px) {
    .hero-overlay h1 {
        font-size: 1.6rem; /* Smaller font size */
    }

    .hero-overlay p {
        font-size: 0.85rem; /* Adjusted paragraph size */
    }

    .hero-overlay {
        padding: 15px 20px; /* Reduced padding */
    }

    .text-section {
        padding: 40px 0; /* Adjusted spacing */
        font-size: 0.9rem;
    }

    .text-section > * {
        padding: 0 10px; /* Reduced padding */
    }

    .text-section h2 {
        font-size: 1.2rem; /* Smaller heading size */
    }

    .image-break {
        height: 35vh; /* Reduced height */
    }
}

